import './criarPercurso.css';
import React from "react";
import Home from '../../../imagens/home.png';
import {useState} from "react";
import {Link} from "react-router-dom";

const CriarPercurso = () => {

    const [armazemPartida,setArmazemPartida]=useState('');
    const [armazemChegada,setArmazemChegada]=useState('');
    const [distancia,setDistancia]=useState('');
    const [duracao,setDuracao]=useState('');
    const [energiaGasta,setEnergiaGasta]=useState('');
    const [tempoExtra,setTempoExtra]=useState('');

    const [formErrors, setFormErrors] = useState({});
    

    function criar() {
        const errors = {};
        let dadosValidos = true;

        if(armazemPartida.length === 0 || armazemChegada.length === 0 ||
            distancia.length === 0 || duracao.length === 0 || energiaGasta.length === 0 ||  tempoExtra.length === 0) {
                errors.camposObrigatorios = "Todos os campos so obrigatrios!"
                dadosValidos = false;
        }
        if(distancia < 0 || duracao < 0 || energiaGasta < 0 || tempoExtra < 0) {
            errors.valoresNumericos = "Distncia, Durao, Energia e Tempo Extra no podem ser negativos!"
            dadosValidos = false;
        }
        setFormErrors(errors);

        if(dadosValidos) {
            pedidoAPILogistica();
        } 
    };


    async function pedidoAPILogistica () {
        await fetch('http://localhost:3000/api/percursos/', {
            method: 'POST',
            body: JSON.stringify({
                "armazemPartida":armazemPartida,
                "armazemChegada":armazemChegada,
                "distancia": distancia,
                "duracao": duracao,
                "energiaGasta": tempoExtra,
                "tempoExtra": energiaGasta
            }),
            headers: {
                'Content-type': 'application/json; charset=UTF-8',
            },
            })
            .then((response) => response.json())
            .then((json) => console.log(json))
            .catch(err => window.alert('No foi possvel criar o percurso. Verifique se os IDs dos Armazns esto corretos e se j no existe nenhum percurso entre esses 2 Armazns.'));
    }

    return (
        <main>
            <div className="main-percurso">   
                <div className="card-percurso">
                    <h1>CRIAR PERCURSO</h1>
                    <div className="textfield-percurso">
                        <label>Armazm de Partida</label>
                        <input type="text" className="inputPercurso" onChange={(e=>setArmazemPartida(e.target.value))}/>
                    </div>
                    <div className="textfield-percurso">
                        <label>Armazm de Chegada</label>
                        <input type="text" className="inputPercurso" onChange={(e=>setArmazemChegada(e.target.value))}/> 
                    </div>
                    <div className="textfield-percurso">
                        <label>Distncia</label>
                        <input type="number" className="inputPercurso" onChange={(e=>setDistancia(e.target.value))}/>
                    </div>
                    <div className="textfield-percurso">
                        <label>Durao</label>
                        <input type="number" className="inputPercurso" onChange={(e=>setDuracao(e.target.value))}/>
                    </div>
                    <div className="textfield-percurso">
                        <label>Energia Gasta</label>
                        <input type="number" className="inputPercurso" onChange={(e=>setEnergiaGasta(e.target.value))}/>
                    </div>
                    <div className="textfield-percurso">
                        <label>Tempo Extra</label>
                        <input type="number" className="inputPercurso" onChange={(e=>setTempoExtra(e.target.value))}/>
                    </div>
                    <div className="errors">
                        <label>{formErrors.camposObrigatorios}</label>
                        <label>{formErrors.valoresNumericos}</label>
                    </div>                    
                    <button className="btn-criar" onClick={criar}>Criar</button>
                </div>
                <Link to="/">
                <button className="btn-home"><img src={Home} height ="70" width="70"/></button>
                </Link>
            </div>
        </main>
    );
}

export default CriarPercurso;