import './criarArmazem.css';
import React from "react";
import Home from '../../../imagens/home.png';
import Warehouse from '../../../imagens/armazem.png';
import {Link } from "react-router-dom";


export default class CriarArmazem extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
          message: ''
        };
      }

      onCreateArmazem=()=>{

        const armazemInfo= JSON.stringify({
            id: this.refs.armazemID.value,
            designcacao: this.refs.designacao.value,
            morada:{
                rua: this.refs.rua.value,
                numeroPorta: this.refs.numPorta.value,
                codigoPostal: this.refs.codPostal.value,
                localidade: this.refs.localidade.value,
                pais: this.refs.localidade.value
            },
            coordenadas: {
                latitude: this.refs.latitude.value,
                longitude: this.refs.longitude.value,
                altitude: this.refs.altitude.value
            }
        });

        var requestOptions = {
            method: 'POST',
            headers: {
                'Content-type': 'application/json; charset=UTF-8',
            },
            body: armazemInfo,
            redirect: 'follow'
        };
          
        fetch("https://localhost:5001/api/Armazem/", requestOptions)
            .then(response => response.text())
            .then(result => console.log(result))
            .catch(window.alert("Os dados introduzidos no possibilitam a criao de um Armazm! Introduza os dados novamente. \n" + 
            "Siga os seguintes formatos:\n" +
            "\t-ID: 3 caracteres alfa-numricos;\n" +
            "\t-Designao: mximo de 50 caracteres;\n" +
            "\t-Rua: nome da rua;\n" +
            "\t-Nmero da Porta: nmero inteiro;\n"+
            "\t-Cdigo-Postal: formato portugus(ex:7854-896);\n"+
            "\t-Localidade: localidade do Armazm;\n" +
            "\t-Pas: pas do Armazm;\n"+
            "\t-Latitude: nmero decimal entre -90.00 e 90.00;\n"+
            "\t-Longitude: nmero decimal entre -180 e 180;\n" +
            "\t-Latitude: nmero decimal."));

        
      }

    render() {
        return (
        <main>
            <div className="main-armazem">   
                <div className="card-armazem">
                    <h1>CRIAR ARMAZEM</h1>
                    <div className="textfield-armazem">
                        <label>ID de Armazm</label>
                        <input type="text" className="inputArmazem" ref="armazemID" maxLength={3} required />
                    </div>
                    <div className="textfield-armazem">
                        <label>Designao</label>
                        <input type="text" className="inputArmazem" ref="designacao" maxLength={50} required/>
                    </div>  
                    <div className="textfield-armazem">
                        <label>Rua</label>
                        <input type="text" className="inputArmazem" ref="rua"/>
                    </div>
                    <div className="textfield-armazem">
                        <label>Numero da Porta</label>
                        <input type="number" className="inputArmazem" ref="numPorta" min={0}/>
                    </div>
                    <div className="textfield-armazem">
                        <label>Cdigo Postal</label>
                        <input type="text" className="inputArmazem" ref="codPostal" pattern='[0-9]{4}-[0-9]{3}'/>
                    </div>
                    <div className="textfield-armazem">
                        <label>Localidade</label>
                        <input type="text" className="inputArmazem" ref="localidade"/>
                    </div>
                    <div className="textfield-armazem">
                        <label>Pas</label>
                        <input type="text" className="inputArmazem" ref="pais"/>
                    </div>
                    <div className="textfield-armazem">
                        <label>Latitude</label>
                        <input type="number" step="0.01" className="inputArmazem" ref="latitude" min={-90} max={90}/>
                    </div>
                    <div className="textfield-armazem">
                        <label>Longitude</label>
                        <input type="number" step="0.01" className="inputArmazem" ref="longitude" min={-180} max={180}/>
                    </div>
                    <div className="textfield-armazem">
                        <label>Altitude</label>
                        <input type="number" step="0.01" className="inputArmazem" ref="altitude" min={0}/>
                    </div>
                    <button className="btn-criar-armazem" onClick={this.onCreateArmazem}>Criar</button>
                </div>
                <div>
                    <Link to="/">
                    <button className="btn-home-armazem" ><img src={Home} height ="70" width="70"/></button>
                    </Link>
                    <Link to="/armazem/menu">
                    <button className="btn-armazem" ><img src={Warehouse} height ="70" width="70"/></button>
                    </Link></div>
            </div>
        </main> 
        );       
    }
}

