import './criarEntrega.css';
import React from "react";
import Home from '../../../imagens/home.png';

export default class CriarEntrega extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            message: ''
        };
    }

    onCreateEntrega=()=>{
        var myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        const entregaInfo= JSON.stringify({
            IdArmazem: {
                "value": this.refs.IdArmazem.value
            },
            DataEntrega: this.refs.DataEntrega.value,
            Peso: this.refs.Peso.value,
            TempoCarregamento: this.refs.TempoCarregamento.value,
            TempoDescarregamento: this.refs.TempoDescarregamento.value
        });

        var requestOptions = {
            method: 'POST',
            headers: {
                'Content-type': 'application/json; charset=UTF-8',
            },
            body: entregaInfo,
            redirect: 'follow'
        };

        fetch("https://localhost:5001/api/Entrega/", requestOptions)
            .then(response => response.text())
            .then(result => console.log(result))
            .catch(error => console.log('error', error));

    }

    render() {
        return (
        <main>
            <div className="main-entrega">   
                <div className="card-entrega">
                    <h1>CRIAR ENTREGA</h1>
                    <div className="textfield-entrega">
                        <label>ID de Armazm</label>
                        <input type="text" className="inputEntrega" ref="IdArmazem"/>
                    </div>
                    <div className="datefield-entrega">
                        <label>Data de Entrega</label>
                        <input type="date" className="inputEntrega" ref="DataEntrega"/>
                    </div>
                    <div className="textfield-entrega">
                        <label>Peso</label>
                        <input type="number" step="0.01" className="inputEntrega" ref="Peso" min={0}/>
                    </div>
                    <div className="textfield-entrega">
                        <label>Tempo de Carregamento</label>
                        <input type="number" step="0.01" className="inputEntrega" ref="TempoCarregamento" min={0}/>
                    </div>
                    <div className="textfield-entrega">
                        <label>Tempo de Descarregamento</label>
                        <input type="number" step="0.01" className="inputEntrega" ref="TempoDescarregamento" min={0}/>
                    </div>
                    <button className="btn-criar-entrega" onClick={this.onCreateEntrega}>Criar</button>
                </div>
                <button className="btn-home"><img src={Home} height ="70" width="70"/></button>
            </div>
        </main>
        );
    }
}



