import './menuPrincipal.css';
import React from "react";
import Home from '../../imagens/home.png';
import {Link } from "react-router-dom";

export default class MenuPrincipal extends React.Component{
    render() {
        return (
        <main>
            <div className="main-menu-armazem">   
                <div className="card-menu-armazem">
                    <h1>MENU ARMAZEM</h1>
                    <Link to="/criarArmazem">
                        <button className="btn-menu-criar-armazem" >Criar Armazem</button>
                    </Link>
                    <Link to="/armazem/listar">
                        <button className="btn-menu-listar-armazem" >Listar Armazem</button>
                    </Link>
                </div>
                <Link to="/">
                <button className="btn-home" ><img src={Home} height ="70" width="70"/></button>
                </Link>
            </div>
        </main> 
        );       
    }
}