import './listarCamiao.css';
import React from 'react';
import { useState } from 'react';

const ListarCamiao = () => {
    async function listarCamiao() {


        let opcaoSelecionada = document.getElementById("opcoes-camiao").value;

        var requestOptions = {
            method: 'GET',
            redirect: 'follow'
        };
          

        const response = await fetch("http://localhost:3000/api/camioes/camioesExistentes", requestOptions)
        const camioes = await response.json();
        
        //no caso de ser necessrio apagar a tabela
        const output = document.getElementById("output");
        while (output.rows.length > 1) {
            output.deleteRow(-1);
        }

        let myMap = new Map(Object.entries(camioes));

        for (let niveis = 0; niveis < myMap.size; niveis++) {
            var tr = document.createElement('tr');
            tr.innerHTML = '<td>' + myMap.get(''+ niveis).nomeCamiao + '</td>' +
            '<td>' + myMap.get(''+ niveis).cargaTotalBaterias + '</td>' +
            '<td>' + myMap.get(''+ niveis).tara + '</td>' +
            '<td>' + myMap.get(''+ niveis).maximoCarga + '</td>' +
            '<td>' + myMap.get(''+ niveis).autonomia + '</td>' +
            '<td>' + myMap.get(''+ niveis).tempoCarregamento + '</td>' +
            '<td>' + myMap.get(''+ niveis).matriculaCamiao + '</td>';

            output.appendChild(tr);

            /*let nomes = myMap.get('' + niveis).nomeCamiao;
            let cargasTotaisBaterias = myMap.get(''+ niveis).cargaTotalBaterias;
            let taras = myMap.get(''+ niveis).tara;
            let maximoCargas = myMap.get(''+ niveis).maximoCarga;
            let autonomias = myMap.get(''+ niveis).autonomia;
            let temposCarregamento = myMap.get(''+ niveis).tempoCarregamento;
            let matriculas = myMap.get('' + niveis).matriculaCamiao;    

            let row = output.insertRow(-1);
            let cell = row.insertCell(-1);
            cell.className = "output";
            cell.innerHTML = nomes;
            cell.innerHTML = cargasTotaisBaterias;
            cell.innerHTML = taras;
            cell.innerHTML = maximoCargas;
            cell.innerHTML = autonomias;
            cell.innerHTML = temposCarregamento;
            cell.innerHTML = matriculas;*/
        }
    }

    return (
        <main>
            <div className="main-listar-camiao">
                <div className="left-listar-camiao">
                    <table id="output">
                        <thead>
                            <tr>
                                <th className="nomeCamiaoTable">Nome</th>
                                <th className="cargaTotalBateriasTable">Carga Total das Baterias</th>
                                <th className="taraTable">Tara</th>
                                <th className="maximoCargaTable">Mximo de Carga</th>
                                <th className="autonomiaTable">Autonomia</th>
                                <th className="tempoCarregamentoTable">Tempo de Carregamento</th>
                                <th className="matriculaCamiaoTable">Matrcula</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div className="right-listar-camiao">
                    <div className="card-listar-camiao">
                        <h1>LISTAR CAMIO</h1>
                        <div className="opcoes-camiao">
                            <label>Opo</label>
                            <select id="opcoes-camiao">
                                <option value="todos">Todos</option>
                            </select>
                        </div>
                        <button className="btn-listar-camiao" onClick={listarCamiao}>LISTAR</button>
                    </div>
                </div>
            </div>
        </main>
    );
}

export default ListarCamiao;