import './listarEntrega.css';
import React from "react";
import {useState} from "react";
//import fetch from 'node-fetch';


const ListarEntrega = () => {

    const [idArmazem,setIdArmazem]=useState('');
    const [dataInicial,setDataInicial]=useState('');
    const [dataFinal,setDataFinal]=useState('');
    const [opcao, setOpcao]=useState('todos');

    const atualizarOpcao = (e) => {
        //let opcaoSelecionada = document.getElementById("opcoes-ent").value;
        let opcaoSelecionada = e.target.value;
        setOpcao(opcaoSelecionada);
        let inputIdArmazem = document.getElementById("idArmazemInput");
        let inputDataInicial = document.getElementById("dataInicialInput");
        let inputDataFinal = document.getElementById("dataFinalInput");
        if(opcaoSelecionada === 'todos') {
            //limparDados();
            inputIdArmazem.setAttribute('readonly', true);
            inputDataInicial.setAttribute('readonly', true);
            inputDataFinal.setAttribute('readonly', true);
        } else if(opcaoSelecionada === 'idArmazem') {
            //limparDados();
            inputIdArmazem.removeAttribute('readonly');
            inputDataInicial.setAttribute('readonly', true);
            inputDataFinal.setAttribute('readonly', true);
        } else {
            //limparDados();
            inputDataInicial.removeAttribute('readonly');
            inputDataFinal.removeAttribute('readonly');
            inputIdArmazem.setAttribute('readonly', true);
        }
        
        
    }

    

    async function listar() {
        let path;
        if(opcao === 'todos') {
            path = '/';
        } else if(opcao === 'idArmazem') {
            path = '/porIdArmazem?idArmazem=' + idArmazem;
        } else if(opcao === 'datas') {
            path = '/porDatas?data1=' + dataInicial + '&data2=' + dataFinal;
        }

        var requestOptions = {
            method: 'GET',
            redirect: 'follow'
        };
          

        const response = await fetch("http://localhost:3000/api/Entrega/" + path, requestOptions)
        const entregas = await response.json();
        
        //no caso de ser necessrio apagar a tabela
        const output = document.getElementById("output");
        while (output.rows.length > 1) {
            output.deleteRow(-1);
        }

        if(opcao === 'datas'){
            
            let row = output.insertRow(-1);

            inserirCell(row, entregas.dataEntrega);

            inserirCell(row, entregas.peso);

            inserirCell(row, entregas.tempoCarregamento);

            inserirCell(row, entregas.tempoDescarregamento);

        } else {

            let myMap = new Map(Object.entries(entregas));

            for(let niveis = 0; niveis <= myMap.size-1; niveis++){

                let row = output.insertRow(-1);

                let dataEntrega = myMap.get('' + niveis).dataEntrega;
                inserirCell(row, dataEntrega);

                let peso = myMap.get('' + niveis).peso;
                inserirCell(row, peso);

                let tempoCarregamento = myMap.get('' + niveis).tempoCarregamento;
                inserirCell(row, tempoCarregamento);

                let tempoDescarregamento = myMap.get('' + niveis).tempoDescarregamento;
                inserirCell(row, tempoDescarregamento);
            }
        }
    }

    function inserirCell(row, atributo) {
        let cell = row.insertCell(-1);
        cell.className = "output";
        cell.innerHTML = atributo;
    }    

    return (
        <main>
            <div className="main-listar-entrega">
                <div className="left-listar-entrega">
                    <table id="output">
                        <thead>
                            <tr>
                                <th className="idArmazemTable">Id de Armazm</th>
                                <th className="dataEntregaTable">Data de Entrega</th>
                                <th className="pesoTable">Peso</th>
                                <th className="tempoCarregamentoTable">Tempo de Carregamento</th>
                                <th className="tempoDescarregamentoTable">Tempo de Descarregamento</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div className="right-listar-entrega">
                    <div className="card-listar-entrega">
                    <h1>LISTAR ENTREGA</h1>
                    <div className="textfield-listar-entrega">
                        <label>Id de Armazm</label>
                        <input id="idArmazemInput" type="text" className="inputEntrega" onChange={(e=>setIdArmazem(e.target.value))} readOnly/>
                    </div>
                    <div className="datefield-listar-entrega">
                        <label>Data Inicial</label>
                        <input id="dataInicialInput" type="date" className="inputEntrega" onChange={(e=>setDataInicial(e.target.value))} readOnly/>
                    </div>
                    <div className="datefield-listar-entrega">
                        <label>Data Final</label>
                        <input id="dataFinalInput" type="date" className="inputEntrega" onChange={(e=>setDataFinal(e.target.value))} readOnly/>
                    </div>
                    <div className="opcoes-entrega">
                        <label>Opo</label>
                        <select id="opcoes-ent" onChange={atualizarOpcao}>
                            <option value="todos">Todos</option>
                            <option value="idArmazem">Por Id de Armazm</option>
                            <option value="datas">Por Datas</option>
                        </select>
                    </div>
                    <button className="btn-listar-ent" onClick={listar}>LISTAR</button>
                    </div>
                </div>
            </div>
        </main>
    );
}

export default ListarEntrega;