import './menuGestorLogistica.css';
import React from "react";
import Home from '../../../imagens/home.png';
import { Link } from "react-router-dom";

export default class MenuGestorLogistica extends React.Component{
    render() {
        return (
        <main>
            <div className="main-menu-gestorLogistica">   
                <div className="card-menu-gestorLogistica">
                    <h1>MENU DO GESTOR DE LOGSTICA</h1>
                        <Link to="/percurso/criar">
                            <button className="btn-menu-criar-percurso" >Criar Percurso</button>
                        </Link>
                        <Link to="/percurso/listar">
                            <button className="btn-menu-listar-percurso" >Listar Percurso</button>
                        </Link>
                </div>
                <Link to="/">
                <button className="btn-home" ><img src={Home} height ="70" width="70"/></button>
                </Link>
            </div>
        </main> 
        );       
    }
}