import './style.css';
import React from "react";
import Camiao from './camiao.svg';


export default function fazerLogin() {
    return (

        <body>
            <div class="main-login">
                <div class="left-login">
                    <h1>Welcome to ElectricGo!<br></br></h1>
                    <img src={Camiao} class="left-login-image" alt="Camiao animaao"></img>
                </div>
                <div class="right-login">
                    <div class="card-login">
                        <h1>LOGIN</h1>
                        <div class="textfield-login">
                            <input type="text" name="utilizador" placeholder="Utilizador"></input>
                        </div>
                        <div class="textfield-login">
                            <input type="password" name="senha" placeholder="Senha"></input>
                        </div>
                        <button class="btn-login">Login</button>
                    </div>
                </div>
            </div>
        </body>
    );
}